function resolveWixTipsTipV1TipsServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'manage._base_domain_': [
      {
        srcPath: '/_api/tips-service',
        destPath: '',
      },
    ],
    _: [
      {
        srcPath: '/_api/tips-service',
        destPath: '',
      },
    ],
    'www.wixapis.com': [
      {
        srcPath: '/tips',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_ecom_tips';

/**
 * Previews a tip for a list of line items.
 *
 *
 * If you provide the Wix Bookings app ID ([SDK](https://dev.wix.com/docs/sdk/articles/work-with-the-sdk/apps-created-by-wix) | [REST](https://dev.wix.com/docs/rest/articles/getting-started/apps-created-by-wix))
 * as `lineItemInfo.catalogReference.appId`, the response includes tip
 * distributions for each associated
 * Bookings staff member ([SDK](https://dev.wix.com/docs/sdk/backend-modules/bookings/staff-members/introduction) | [REST](https://dev.wix.com/docs/rest/business-solutions/bookings/staff-members/staff-member-object)).
 *
 * If you don't provide a `tipSettingsId`, the `staffDistributionMethod` is
 * based on the site's default tip settings.
 */
export function previewTip(payload: object): RequestOptionsFactory<any> {
  function __previewTip({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.tips.v1.tip',
      method: 'POST' as any,
      methodFqn: 'wix.tips.tip.v1.TipsService.PreviewTip',
      packageName: PACKAGE_NAME,
      url: resolveWixTipsTipV1TipsServiceUrl({
        protoPath: '/v1/tips/preview',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'tip.distributions.createdDate' },
              { path: 'tip.distributions.transaction.date' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __previewTip;
}

